<template>
    <div>
        <h1 class="divided">成长指南</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <div>
              <tabs mode="horizon" @clickTab="getTabInfo" :defaultTabId="defaultTab">
                <tab class="tab" v-for="(item,index) in result.tabs" :key="index" :title="item.categoryName" :id="item._CategoryMID">
                  <div v-if="pannelLoading">
                      <load-indicator loadText="正在加载中"></load-indicator>
                  </div>
                    <div v-else>
                      <div>下列以战士职业基础展示</div>
                      <table style="overflow-x:auto;">
                      <thead>
                        <th>标题</th>
                        <th>描述</th>
                        <th>奖励</th>
                      </thead>
                      <tbody>
                        <tr v-for="(v,k) in listInfo" :key="k" style="height:80px;">
                          <td>{{v.title}}</td>
                          <td style="white-space: pre-wrap;">{{v.tip}}</td>
                          <td>
                            <div class="Title">
                                <div v-for="(vv,kk) in v.mailItem" :key="kk" class="titleitem">
                                    <item-icon-tooltip
                                      :itemId="vv.item.id" 
                                      :itemStub="vv.item"
                                      :count="vv.count"
                                      :noLink="noLink">
                                    </item-icon-tooltip>
                                </div>
                            </div>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                    </div>
                </tab>
              </tabs>
            </div>    
        </template>
        <back-to-top class="topButton"><i class="fa fa-arrow-up"></i></back-to-top>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
import BackToTop from '@/components/util/BacktoTop';
import Tab from "@/components/Tab/Tab";
import Tabs from "@/components/Tab/Tabs";
Vue.component("load-indicator", Loader);
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip,
        BackToTop,
        Tab,
        Tabs,
    },
    data(){
        return{
            result:{},
            noLink:false,
            loading:true,
            categoryId : 0,
            defaultTab: '0',
            listInfo: [],
            tabcache:[],
            pannelLoading:true,
        }
    },
    watch:{
      categoryId(val){
        this.getcategoryInfo(val)
      }
    },
    methods:{
        fetchData(){
          GameInfo.Growup(this.categoryId,this.$store.state.regionCode,
                  (res) => {
                      this.result = res;
                      this.loading = false;
                      this.categoryId = res.tabs.shift()._CategoryMID;
                  },
                  (err) => {
                      this.error = err;
                      this.loading = false;
                  });
            
        },
        getcategoryInfo(categoryId){
            if(this.tabcache[categoryId]){
                this.listInfo = this.tabcache[categoryId];
                this.pannelLoading = false;
            }else{
              GameInfo.Growup(categoryId,this.$store.state.regionCode,
                (res) => {
                    this.listInfo = res.list;
                    this.tabcache[categoryId] = res.list
                    this.$set(this,'listInfo',res.list)
                    this.pannelLoading = false;
                },
                (err) => {
                    this.error = err;
                    this.pannelLoading = false;
                });
            }
        },
        getTabInfo(e){
          if(e>0){
            this.pannelLoading = true;
            this.categoryId = e;
          }
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}

.labryinth-table{
    margin:0 auto;
}
</style>